import {
  Box,
  Button,
  Checkbox,
  Divider,
  Flex,
  FormControl,
  FormLabel,
  HStack,
  Input,
  Radio,
  RadioGroup,
  Select,
  Stack,
  TagLabel,
  Text,
  useColorModeValue,
} from '@chakra-ui/react';
import React from 'react';

import { AppContext } from '../AppContext';

const Checkout = () => {
  const { setCurrentScreen } = React.useContext(AppContext);

  const nextStep = () => {
    setCurrentScreen('second');
  };

  return (
    <Box>
      <Stack spacing={{ base: '2', md: '3' }}>
        {/* <FormControl id="name">
          <Input
          name="name"
          placeholder="Your first and last name"
          focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
          />
        </FormControl> */}
        <FormControl id="name">
          <FormLabel
            fontWeight="semibold"
            color={useColorModeValue('black', 'black')}
          >
            Contact
          </FormLabel>
          <HStack spacing="2">
            <FormControl id="name" maxW="48">
              {/* <FormLabel color={useColorModeValue('gray.700', 'gray.200')}>
                Zip Code
              </FormLabel> */}
              <Input
                name="zip"
                placeholder="First & last name"
                focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
              />
            </FormControl>
            <FormControl id="phone">
              {/* <FormLabel color={useColorModeValue('gray.700', 'gray.200')}>
                Phone number
              </FormLabel> */}
              <Input
                name="phone"
                placeholder="Phone number"
                focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
              />
            </FormControl>
          </HStack>
        </FormControl>

        <FormControl id="email">
          {/* <FormLabel color={useColorModeValue('gray.700', 'gray.200')}>
                Phone number
              </FormLabel> */}
          <Input
            name="email"
            placeholder="Email"
            focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
          />
        </FormControl>
        <HStack>
          <Divider mt={2} mb={2} />
        </HStack>

        <FormControl mt={4} id="street">
          <FormLabel
            fontWeight="semibold"
            color={useColorModeValue('black', 'black')}
          >
            Delivery
          </FormLabel>
          <HStack spacing="2">
            <FormControl id="name" maxW="48">
              {/* <FormLabel color={useColorModeValue('gray.700', 'gray.200')}>
                Zip Code
              </FormLabel> */}
              <Input
                name="zip"
                placeholder="Address"
                focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
              />
            </FormControl>
            <FormControl id="phone">
              {/* <FormLabel color={useColorModeValue('gray.700', 'gray.200')}>
                Phone number
              </FormLabel> */}
              <Input
                name="phone"
                placeholder="Bldg, floor, etc."
                focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
              />
            </FormControl>
          </HStack>
        </FormControl>

        <HStack spacing="2">
          <FormControl id="zip" maxW="48">
            <Input
              name="zip"
              placeholder="Postal Code"
              focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
            />
          </FormControl>
          <FormControl id="city">
            <Input
              name="city"
              placeholder="City"
              focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
            />
          </FormControl>
        </HStack>
        <FormControl id="country">
          <Select
            focusBorderColor={useColorModeValue('gray.500', 'gray.200')}
            color={useColorModeValue('gray.600', 'gray.200')}
            // borderColor={useColorModeValue('gray.500', 'gray.00')}
            placeholder="Country"
          >
            <option value="option1">Option 1</option>
            <option value="option2">Option 2</option>
            <option value="option3">Option 3</option>
          </Select>
          {/* <Input
            name="country"
            placeholder="Country"
            focusBorderColor={useColorModeValue('blue.500', 'blue.200')}
          /> */}
        </FormControl>
        <HStack>
          <Divider mt={2} mb={2} />
        </HStack>

        <FormControl mt={4} id="street">
          <FormLabel
            fontWeight="semibold"
            color={useColorModeValue('black', 'black')}
          >
            Shipping Method
          </FormLabel>
          <HStack spacing="2">
            <FormControl id="name">
              <RadioGroup defaultValue="1">
                <Stack>
                  <HStack justify="space-between">
                    <Radio value="1">
                      {' '}
                      <Text ml={1} fontWeight={'semibold'}>
                        Express Shipping
                      </Text>
                    </Radio>

                    <Text fontWeight={'semibold'}>$59</Text>
                  </HStack>
                  <HStack justify="space-between">
                    <Radio value="2">
                      {' '}
                      <Text ml={1}>Standard Shipping</Text>
                    </Radio>
                    <Text>$20</Text>
                  </HStack>
                </Stack>
              </RadioGroup>
            </FormControl>
          </HStack>
        </FormControl>

        {/* <Checkbox defaultChecked spacing="4" colorScheme="blue">
          Billing address is same as shipping
        </Checkbox> */}

        <HStack>
          <Divider mt={2} mb={2} />
        </HStack>

        <Button
          onClick={nextStep}
          bgColor={useColorModeValue('gray.700', 'blue.800')}
          borderRadius="md"
          variant="primary"
          fontWeight={'normal'}
        >
          Go to payment
        </Button>
      </Stack>
    </Box>
  );
};

export default Checkout;
